<template>
    <div class="wyu-img-uploads">
        <div v-if="!multi" class="wyu-img-one">
            <div class="wyu-upload-div" :style="'width:'+myW+'px;line-height:'+myL+'px; height:'+myH+'px;'">
                <div  v-if="showImg == ''" @click="uploadFile"  class="image-box">
                    <i class="el-icon-plus"></i>
                </div>
                <div  v-if="showImg!=''"  @click="uploadFile" :style="'background-image: url('+showImg+');'"  class="image-box">
                </div>
                <i  v-if="showImg!=''"  @click="deleteImg" class="el-icon-error"></i>
            </div>
        </div>
        <div v-if="multi" class="wyu-img-list">

            <div v-for="(item,index) in showImgs" :key="index" class="wyu-upload-div"  :style="'width:'+myW+'px;line-height:'+myL+'px; height:'+myH+'px;'">
                <div :style="'background-image: url('+item+');'"  class="image-box">
                </div>
                <i @click="deleteImg(index)" class="el-icon-error"></i>
            </div>

            <div @click="uploadFile"  class="wyu-upload-div"  :style="'width:'+myW+'px;line-height:'+myL+'px; height:'+myH+'px;'">
                <i class="el-icon-plus"></i>
            </div>
        </div>

        <el-dialog
                title="图片相册"
                :visible.sync="dialogVisible"
                :modal-append-to-body="false"
                width="800px"
        >
            <div v-if="showTab == 0" class="photo-upload-tab"  v-loading="loading">
                <div class="photo-upload-btn">
                    <el-button type="primary" @click="showTab = 1" size="small">上传图片</el-button>
                </div>

                <div class="photo-main">
                    <div class="photo-group">
                        <ul>
                            <li v-for="(item,index) in groups" :key="index" :class="item.id == cid ? 'on' : ''" @click="clickCategory(item.id)">{{item.name}}</li>
                        </ul>
                    </div>
                    <div v-if="multi" class="photo-list">
                        <div v-for="(item,index) in photos" :key="index"  @click="setSelectImgs(item.photo)" :class="isInArray(item.photo) !==false ? 'item on' :'item'">
                            <div class="image-box" :style="'background-image: url('+item.photo+');'"></div>
                            <label><i class="el-icon-upload-success el-icon-check"></i></label>
                        </div>
                    </div>
                    <div v-if="!multi" class="photo-list">
                        <div v-for="(item,index) in photos" :key="index" @click="selectImg =  item.photo" :class="selectImg == item.photo ? 'item on' : 'item'">
                            <div class="image-box" :style="'background-image: url('+item.photo+');'"></div>
                            <label v-if="selectImg == item.photo"><i class="el-icon-upload-success el-icon-check"></i></label>
                        </div>
                    </div>
                </div>
                <div style="height:40px;">
                    <div class="pagegroup">
                        <el-pagination
                                :page-size="limit"
                                @current-change="handleCurrentChange"
                                layout="total, prev, pager, next"
                                :total="total">
                        </el-pagination>
                    </div>
                </div>

                <div style="text-align:center; margin-top:10px;">
                    <el-button  size="small" type="primary" @click="selectPhoto()">确定选取</el-button>
                    <el-button  size="small" type="danger" @click="clearSelect()">取消选择</el-button>
                </div>
            </div>

            <div v-if="showTab == 1" class="photo-upload-tab">
                <el-form ref="form"  label-width="80px">
                    <el-form-item label="选择分组">
                        <el-select v-model="groupId" style="float:left;" size="small" placeholder="请选择分组">
                            <el-option v-for="(item,index) in groups" :key="index" :label="item.name" :value="item.id"></el-option>
                        </el-select>
                        <create-resource-category v-if="groups.length < 16" @addYes="getCategoryByAdd"></create-resource-category>
                    </el-form-item>
                    <el-form-item label="上传图片">
                        <el-upload
                                class="avatar-uploader"
                                :action="uploadImgUrl"
                                :show-file-list="false"
                                :on-success="handleAvatarSuccess"
                                :before-upload="beforeAvatarUpload">
                            <img v-if="imageUrl" :src="imageUrl" class="avatar">
                            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                        </el-upload>
                        <p>只能上传JPG或者PNG的图片</p>
                    </el-form-item>
                </el-form>
                <div style="text-align:center; margin-top:10px;">
                    <el-button  size="small" :disabled="imageUrl ? false:true" @click="selectPhotoByAdd()">确定选取</el-button>
                    <el-button  size="small" @click="showTab=0">返回列表</el-button>
                </div>
            </div>

        </el-dialog>
    </div>
</template>

<script>
    import * as setting from '../../public/config.js';
    import createResourceCategory from '@/pages/resource/category/components/create';
    import * as api from '@/api.js';
    //multi true 代表多图  否则为单图
    export default {
        name: 'imgUploads',
        props:['multi','img','w','h'],
        components:{
            createResourceCategory
        },
        data:function(){
            return  {
                myW:this.w ? this.w : 120,
                myH:this.h ? this.h : 120,
                myL:this.h ? this.h - 4 : 116,
                uploadImgUrl:'',
                showImg:'',
                showImgs:[],
                imageUrl:'',
                dialogVisible:false,
                selectImg:'',//选中的单图
                selectImgs:[],//选中的集合图片
                showTab:0,
                groupId:0,//下拉菜单中的选中样式
                cid:0, //图片列表中的选中样式
                groups:[],
                page:1,
                loading:false,
                total:0,
                limit:12,
                photos:[],//图片列表
            };
        },
        watch:{
            img:function(val){
                if(this.multi == true){ //如果是多选
                    if(this.img != '' && this.img != this.showImgs){
                        this.showImgs = this.img;
                    }
                }else{
                    this.showImg = this.img; //单图的情况
                }
            },
            showImg:function(val){
                if(val != this.img){ //回调做您想做的事情
                    this.$emit('input',val);
                }
            },
            showImgs:function(val){
                if(val != this.img){ //回调做您想做的事情
                    this.$emit('input',val);
                }
            },
            groupId:function(val){
                let host = "" ;
                if(process.env.NODE_ENV == "development"){
                    host = "https://open.woyaou.com/";
                }else{
                    host = "/";
                }
                this.uploadImgUrl = host +'shop/admin.upload/imageUpload?appToken='+encodeURIComponent(this.$store.getters.appToken)+'&groupId='+val;
            }
        },
        created(){
            if(this.multi == true){ //如果是多选
                if(this.img != '' && this.img != this.showImgs){
                    this.showImgs = this.img;
                }
            }else{
                this.showImg = this.img; //单图的情况
            }
            let host = '';
            if(process.env.NODE_ENV == "development"){
                host = "https://open.woyaou.com/";
            }else{
                host = "/";
            }
            this.uploadImgUrl = host +'shop/admin.upload/imageUpload?appToken='+encodeURIComponent(this.$store.getters.appToken);
        },
        methods:{
            setSelectImgs(img){
                var a  = this.isInArray(img);
                if(a!==false){
                    this.selectImgs.splice(a,1);
                }else{
                    this.selectImgs.push(img);
                }
            },
            isInArray(img){
                for(var i = 0; i < this.selectImgs.length; i++){
                    if(img == this.selectImgs[i]){
                        return i;
                    }
                }
                return false;
            },
            selectPhotoByAdd(){
                if(this.multi == true){
                    this.showImgs.push(this.imageUrl);
                }else{ //如果是单选
                    this.showImg = this.imageUrl;
                }
                this.imageUrl = '';
                this.dialogVisible = false;
            },
            selectPhoto(){
                if(this.multi == true){ //如果是多选
                    let imgs =  JSON.parse(JSON.stringify(this.showImgs));
                    for(var a in   this.selectImgs){
                        imgs.push(this.selectImgs[a]);
                    }
                    this.showImgs = imgs;
                    this.selectImgs = [];
                }else{ //如果是单选
                    this.showImg = this.selectImg;
                    this.selectImg = '';
                }
                this.dialogVisible = false;
            },
            clearSelect(){
                this.selectImg = '';
                this.selectImgs = [];
            },
            clickCategory(id){
                this.cid = id;
                this.page = 1;
                this.count = 0;
                this.getPhotos();
            },
            handleCurrentChange(val){
                this.page = val;
                this.getPhotos();
            },
            getPhotos(){
                this.loading = true;
                api.post('shop/admin.resource.resource/index',{
                    appToken:this.$store.getters.appToken,
                    category_id:this.cid,
                    limit:this.limit,
                    page:this.page,
                }).then(res=>{
                    this.photos = res.list;
                    this.total = res.count;
                    this.loading = false;
                }).catch((error) => {
                    this.loading = false;
                });
            },
            getCategoryByAdd(id){
                this.apiGetCategory(id);
            },
            getCategory(){  //取得分类


                this.apiGetCategory();

            },
            apiGetCategory(id){ //从数据库中取得分类
                let local = [{name:'全部图片',id:0}];
                api.post('shop/admin.resource.category/index',{
                    appToken:this.$store.getters.appToken,
                }).then(res=>{

                    for(var a in res.list){
                        local.push({
                            name:res.list[a].title,
                            id:res.list[a].category_id,
                        });
                    }
                    this.groups = local;
                    if(id){
                        this.groupId = id;
                    }
                }).catch((error) => {

                });
            },

            uploadFile(){
                this.getCategory();
                this.getPhotos();
                this.dialogVisible = true;
            },
            deleteImg(index){
                if(this.multi == true){
                    this.showImgs.splice(index,1);
                }else{
                    this.showImg = '';
                }
            },
            handleAvatarSuccess(res, file) {
                this.imageUrl = res.data.img_url;
                this.page = 1;
                this.cid = 0;
                this.getPhotos();
            },
            beforeAvatarUpload(file) {
                const isLt2M = file.size / 1024 / 1024 < 2;
                if (!isLt2M) {
                    this.$message.error('上传头像图片大小不能超过 2MB!');
                }
                return  isLt2M;
            }
        }
    }
</script>

<style>
    .avatar-uploader .el-upload{
        border: 1px dashed #d9d9d9;
        border-radius: 6px;
        cursor: pointer;
        position: relative;
        overflow: hidden;
    }
    .avatar-uploader .el-upload:hover {
        border-color: #409EFF;
    }
    .avatar-uploader-icon {
        font-size: 28px;
        color: #8c939d;
        width: 120px;
        height: 120px;
        line-height: 120px;
        text-align: center;
    }
    .avatar {
        width: 120px;
        height: 120px;
        display: block;
    }
    .wyu-img-uploads{
        height: auto;
        overflow: hidden;
    }
    .wyu-upload-div{
        background-color: #fbfdff;
        border: 1px dashed #c0ccda;
        border-radius: 6px;
        box-sizing: border-box;


        cursor: pointer;
        font-size: 20px;

        vertical-align: top;
        text-align: center;
        position: relative;
        float: left;

    }

    .wyu-img-list   .wyu-upload-div{
        margin-right: 10px;
        margin-top: 10px;
    }

    .wyu-upload-div:hover{
        border-color: #005DE5;
    }
    .wyu-upload-div .image-box{
        width: 100%;
        height: 100%;
        background-size: cover;
        background-position: 50% 50%;
    }

    .photo-upload-tab{
        height: auto;
        overflow: hidden;
    }
    .wyu-upload-div .el-icon-error{
        font-size: 20px;
        position:absolute;
        right: -5px;
        top: -5px;
        display: none;
        z-index: 10;
        color:#005DE5;
    }
    .wyu-upload-div:hover .el-icon-error{
        display: block;
    }

    .photo-main{
        width: 100%;
        height: auto;
        overflow: hidden;
        margin-top: 10px;
    }

    .pagegroup{
        float: right;
        margin-right: 10px;
    }

    .photo-group{
        float: left;
        width: 100px;
        background: #f2f2f2;
        height: auto;
        min-height: 300px;
        position: relative;
    }
    .photo-group li{
        padding: 8px 10px;
        background: #fff;
        border-bottom: 1px solid #f2f2f2;
        cursor: pointer;
    }
    .photo-group li b{
        float: right;
        margin-right: 5px;
    }
    .photo-group li.on{
        background: #999999;
        color: #fff;
    }

    .photo-list{
        float: left;
        margin-left: 50px;
        width: 600px;
        height: 450px;
        overflow-y: scroll;
    }

    .photo-list .item{
        position: relative;
        float: left;
        width: 136px;
        height: 136px;
        margin-right: 10px;
        margin-bottom: 10px;
        cursor: pointer;
        background: #f2f2f2;
        overflow: hidden;
        box-sizing: border-box;
        border:1px dotted #f2f2f2;
    }


    .photo-list .item .image-box{
        background: #ccc;
        width: 100%;
        height: 100%;
        background-size: cover;
        background-position: 50% 50%;
    }

    .photo-list .item  label{
        position: absolute;
        right: -15px;
        top: -6px;
        width: 40px;
        height: 24px;
        background: #13ce66;
        text-align: center;
        transform: rotate(45deg);
        box-shadow: 0 0 1pc 1px rgba(0,0,0,.2);
        display: none;
    }

    .photo-list .item label i{
        font-size: 12px;
        margin-top: 11px;
        transform: rotate(-45deg);
        color: #fff;
    }

    .photo-list .item.on{
        border: 2px solid #13ce66;
    }
    .photo-list .item.on label{
        display: block;
    }
</style>
