<template>
  <div
     v-loading="loading"
     element-loading-spinner="el-icon-loading"
     element-loading-background="rgba(0, 0, 0, 0.2)"
     class="addCategory">
      <el-button v-if="status == 0"  @click="status=1" type="text">添加分组</el-button>
      <el-input v-if="status == 1"  v-model="groupname" style="float:left;width:100px;" size="small" placeholder="输入分组名称"></el-input>
