<template>
    <div>
        <top-nav title="抢购活动列表"></top-nav>
        <div class="wyu-tips wyu-tips-warning">
            <p> 1、 首先添加抢购活动</p>
            <p> 2、 然后点击活动商品为抢购活动添加关联商品</p>
            <p> 3、 删除(释放产品) 是指删除当前活动并解除当前活动下已关联的商品与活动的关联状态，即商品恢复未被活动选用状态，可被任意活动选择并关联</p>
        </div>
        <div class="wyu-main">
            <div class="wyu-block" v-loading="loading">
                <el-form size="mini" v-model="searchForm" class="search-form" :inline="true">
                    <el-form-item label="抢购ID">
                        <el-input v-model="searchForm.rushbuy_id" placeholder="抢购ID"></el-input>
                    </el-form-item>

                    <el-form-item label="抢购活动标题">
                        <el-input v-model="searchForm.name" placeholder="抢购活动标题"></el-input>
                    </el-form-item>

                    <el-form-item label="是否上线中">
                        <el-select v-model="searchForm.is_online" placeholder="请选择">
                            <el-option label="请选择" :value="-999"></el-option>
                            <el-option label="已下线" :value="0"></el-option>
                            <el-option label="上线中" :value="1"></el-option>
                        </el-select>
                    </el-form-item>

                    <el-form-item label="添加日期">
                        <date-between v-model="searchForm.adddate"></date-between>
                    </el-form-item>

                    <el-form-item label="开始日期">
                        <date-between v-model="searchForm.startdate"></date-between>
                    </el-form-item>

                    <el-form-item label="结束日期">
                        <date-between v-model="searchForm.enddate"></date-between>
                    </el-form-item>

                    <div style="padding-left:20px;">
                        <el-button size="mini" type="primary" @click="getList(1)">搜索</el-button>
                        <el-button size="mini" type="text" @click="searchForm.rushbuy_id='',searchForm.goods_id='',searchForm.name='',
                        searchForm.is_online=-999,searchForm.adddate='',searchForm.startdate='',searchForm.enddate='',getList(1)">取消搜索</el-button>
                        <el-button size="mini" type="danger" @click="dialog = true">添加抢购活动</el-button>
                    </div>
                </el-form>
                <el-table :data="datas.list" style="width: 100%" size="small" @selection-change="handleSelectionChange">
                    <el-table-column type="selection" width="55"></el-table-column>

                    <el-table-column prop="rushbuy_id" label="抢购ID" width="60"></el-table-column>

                    <el-table-column prop="name" label="标题" >
                        <template slot-scope="scope">
                            <div class="ellipsis-02">{{scope.row.name}}</div>
                        </template>
                    </el-table-column>

                    <el-table-column label="封面图片" width="150">
                        <template slot-scope="scope">
                            <img width="60" :src="scope.row.prom_img"/>
                        </template>
                    </el-table-column>

                    <el-table-column label="已售数量"  width="160">
                        <template slot-scope="scope">
                            {{scope.row.order_num}}
                        </template>
                    </el-table-column>

                    <el-table-column label="抢购有效期" width="200">
                        <template slot-scope="scope">
                            <p>开始时间: {{scope.row.start_time}}</p>
                            <p>结束时间: {{scope.row.end_time}}</p>
                        </template>
                    </el-table-column>

                    <!--<el-table-column label="最低抢购价" v-if="rushbuy_price > 0 && vip_rushbuy_price > 0" width="150">-->
                        <!--<template slot-scope="scope">-->
                            <!--<p>注册会员：<b style="color: #D94854">￥ {{scope.row.rushbuy_price}}</b> </p>-->
                            <!--<p>PLUS会员：<b style="color: #D94854">￥ {{scope.row.vip_rushbuy_price}}</b> </p>-->
                        <!--</template>-->
                    <!--</el-table-column>-->

                    <el-table-column prop="orderby" label="排序" width="80"></el-table-column>


                    <el-table-column prop="goods_sn" label="活动开启" width="80">
                        <template slot-scope="scope">
                            <el-switch @change="setOnline(scope)" :value="scope.row.is_online" :inactive-value="0" :active-value="1" ></el-switch>
                        </template>
                    </el-table-column>


                    <el-table-column label="添加日期/IP" width="200">
                        <template slot-scope="scope">
                            <p>日期：{{scope.row.add_time}}</p>
                            <p>IP：{{scope.row.add_ip}}</p>
                        </template>
                    </el-table-column>

                    <el-table-column label="操作" width="300">
                        <template slot-scope="scope">
                            <el-col :span="8">
                                <router-link :to="'/marketing/rushbuy/goods/' + scope.row.rushbuy_id">
                                    <el-button size="mini" type="text">管理商品</el-button>
                                </router-link>
                                &nbsp;
                                <el-button @click="dialog = true,getDetail(scope)" size="mini" type="text">编辑</el-button>
                            </el-col>

                            <el-col :span="8" style="text-align: center">
                                <el-popover placement="top" v-model="scope.row.is_delete">
                                    <p style="margin-bottom: 4px">确定删除吗？</p>
                                    <div style="margin: 0">
                                        <el-button :loading="loading" size="mini" type="text" @click="scope.row.is_delete = false">取消</el-button>
                                        <el-button :loading="loading" type="primary" size="mini" @click="del(scope)">确定</el-button>
                                    </div>
                                    <el-button type="text" size="mini" slot="reference">删除（释放产品）</el-button>
                                </el-popover>
                            </el-col>
                        </template>
                    </el-table-column>
                </el-table>
                <div class="page">
                    <div class="page-mian">
                        <el-pagination @current-change="getList" :page-size="datas.limit"
                                       layout="total, prev, pager, next" :total="datas.count">
                        </el-pagination>
                    </div>
                </div>
            </div>
        </div>
        <!--编辑新增 弹窗-->
        <el-dialog title="基本信息" :modal="false" v-loading="subLoading" :visible.sync="dialog" width="50%" :modal-append-to-body="false">
            <el-form :model="detail" style="padding: 0" size="mini" label-width="100px">
                <el-form-item label="标题">
                    <el-input v-model="detail.name" style="width: 500px;"></el-input>
                    <p class="wyu-form-tips"></p>
                </el-form-item>

                <el-form-item label="封面缩略图" prop="prom_img">
                    <img-uploads :img="detail.prom_img" v-model="detail.prom_img" w="100" h="100"></img-uploads>
                    <p class="wyu-form-tips">建议尺寸 375px*188px 或等比缩放</p>
                </el-form-item>

                <el-form-item label="抢购开始时间">
                    <el-date-picker v-model="detail.start_time" type="datetime" placeholder="选择日期时间" align="right" :picker-options="pickerOptions1"></el-date-picker>
                </el-form-item>

                <el-form-item label="抢购结束时间">
                    <el-date-picker v-model="detail.end_time" type="datetime" placeholder="选择日期时间" align="right" :picker-options="pickerOptions1"></el-date-picker>
                </el-form-item>

                <el-form-item label="排序">
                    <el-input v-model="detail.orderby" style="width: 500px;"></el-input>
                </el-form-item>

                <el-form-item label="是否开启">
                    <el-switch v-model="detail.is_online" :active-value="1" :inactive-value="0"></el-switch>
                </el-form-item>

                <div class="wyu-title">
                    <h2>SEO设置</h2>
                </div>
                <el-form-item label="百度SEO标题" label-width="120px">
                    <el-input v-model="detail.seo.title" style="width:500px;"  autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="百度SEO描述" label-width="120px">
                    <el-input v-model="detail.seo.keywords" type="textarea" :autosize="{minRows: 2, maxRows: 5}" style="width:500px;" autocomplete="off"  placeholder="请输入内容" ></el-input>
                </el-form-item>
                <el-form-item label="百度SEO关键词" label-width="120px">
                    <el-input v-model="detail.seo.description" type="textarea" :autosize="{minRows: 5, maxRows: 10}" style="width:500px;" autocomplete="off"  placeholder="请输入内容"></el-input>
                </el-form-item>
                <el-form-item label="百度SEO原始发布时间" label-width="120px">
                    <el-date-picker v-model="detail.seo.releaseDate" type="date" placeholder="选择日期"></el-date-picker>
                </el-form-item>
                <el-form-item label="百度封面图片" label-width="120px">
                    <img-uploads :multi="true" :img="detail.seo.image"  v-model="detail.seo.image"></img-uploads>
                    <p class="tit"> 用于信息流投放后的封面显示，最多3张，单图片最大2M；封面图建议尺寸：高>=210px 宽>=375px;最小尺寸: 高>=146px 宽>=218px</p>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button size="small" @click="dialog = false">取 消</el-button>
                <el-button size="small" type="primary" @click="setDetail(true)">确 定</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    import TopNav from "@/components/TopNav";
    import * as api from "@/api";
    import DateBetween from "@/components/DateBetween";
    import ImgUploads from "@/components/Photos";

    export default {
        name: 'goodsAttrIndex',
        components: {TopNav,DateBetween,ImgUploads},
        data() {
            return {
                //整体菊花
                loading: false,
                dialog: false,
                subLoading: false,
                //列表数据
                datas: [],
                //搜索数据
                searchForm: {
                    bargain_id: '',
                    goods_id: '',
                    name: '',
                    adddate: [],
                    is_online: -999,
                },
                detail: {
                    seo: {
                        title: '',
                        keywords: '',
                        description: '',
                        releaseDate: '',
                        image: [],
                    },
                },
                pickerOptions1: {
                    shortcuts: [{
                        text: '今天',
                        onClick(picker) {
                            picker.$emit('pick', new Date());
                        }
                    }, {
                        text: '昨天',
                        onClick(picker) {
                            const date = new Date();
                            date.setTime(date.getTime() - 3600 * 1000 * 24);
                            picker.$emit('pick', date);
                        }
                    }, {
                        text: '一周前',
                        onClick(picker) {
                            const date = new Date();
                            date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
                            picker.$emit('pick', date);
                        }
                    }]
                },
            }
        },

        created() {
            this.getList();
        },
        methods: {


            getList(page = 1) {
                this.loading = true;
                api.post("/shop/admin.marketing.rushbuy/index", {
                    appToken: this.$store.getters.appToken,
                    rushbuy_id: this.searchForm.rushbuy_id,
                    name: this.searchForm.name,
                    goods_id: this.searchForm.goods_id,
                    bg_add_time: this.searchForm.adddate[0],
                    end_add_time: this.searchForm.adddate[1],
                    is_online: this.searchForm.is_online,
                    page: page,
                }).then(res => {
                    this.datas = res;
                    this.loading = false;
                }).catch((error) => {
                    this.loading = false;
                });
            },



            getDetail(scope) {
                this.detail = scope.row;
            },




            setDetail(sub = true) {
                if (sub == false) {
                    this.dialog = true;
                    this.detail.rushbuy_id = '';
                    this.detail.name = "";
                    this.detail.prom_img = "";
                    this.detail.is_online = "";
                    this.detail.end_time = "";
                    this.detail.start_time = "";
                    this.detail.orderby = "";
                    this.detail.is_vip = "";
                    this.detail.seo = {
                        title: '',
                        keywords: '',
                        description: '',
                        releaseDate: '',
                        image: [],
                    };

                    return false;
                } else {
                    this.subLoading = true;
                    var url = "/shop/admin.marketing.rushbuy/create";
                    if (this.detail.rushbuy_id > 0) {
                        url = "/shop/admin.marketing.rushbuy/edit";
                    }
                    api.post(url, {
                        appToken: this.$store.getters.appToken,
                        rushbuy_id: this.detail.rushbuy_id,
                        name: this.detail.name,
                        prom_img: this.detail.prom_img,
                        is_online: this.detail.is_online,
                        end_time: this.detail.end_time,
                        start_time: this.detail.start_time,
                        orderby: this.detail.orderby,
                        is_vip: this.detail.is_vip,
                        seo: JSON.stringify(this.detail.seo),
                    }).then(res => {
                        this.subloading = false;
                        this.dialog = false;
                        this.$message({
                            message: '提交成功',
                            type: 'success'
                        });
                        this.getList(1);
                    }).catch((error) => {
                        this.loading = false;
                    });
                }

            },


            handleSelectionChange(val) {
                let Ids = [];
                for (let i in val) {
                    Ids.push(i);
                }
                this.rushbuyIds = Ids;
            },



            setOnline(scope){
                this.loading = true;
                let is_online = 0;
                if (scope.row.is_online == 0) {
                    is_online = 1;
                }
                api.post("/shop/admin.marketing.rushbuy/online", {
                    appToken: this.$store.getters.appToken,
                    rushbuy_id: scope.row.rushbuy_id,
                    is_online: is_online,
                }).then(res => {
                    this.getList();
                    this.loading = false;
                }).catch((error) => {
                    this.loading = false;
                });
            },


            del(scope) {
                this.loading = true;
                api.post("/shop/admin.marketing.rushbuy/del", {
                    appToken: this.$store.getters.appToken,
                    rushbuy_id : scope.row.rushbuy_id,
                }).then(res => {
                    this.getList();
                    this.loading = false;
                }).catch((error) => {
                    this.loading = false;
                });
            },
        }
    }
</script>

